    width: 40vw; }
  .quote__container {
    margin-left: 6%; }
    .quote__container blockquote {
      width: 80vw; }
  .quote-card {
    margin-bottom: 70px; }
    .quote-card img {
      opacity: 0.8; }
  p {
    max-width: 70%; } }

.section__container {
  padding-top: 20%;
  margin-left: 10%;
  margin-right: 10%; }

.tagline {
  text-align: left; }

.content__container {
  margin-left: 10%;
  margin-right: 10%;
  margin-top: 20px;
  margin-bottom: 60px; }

.tagline__underline {
  height: 3px;
  margin: 0;
  width: 30px;
  border: none;
  text-align: left;
  -webkit-transition: width .2s;
  transition: width .2s; }
  .tagline__underline_large {
    width: 125px; }
  .tagline__underline_yellow {
    background: #fc3; }
  .tagline__underline_grey {
    background: #989898; }
  .tagline__underline_black {
    background: black; }

.header h1 {
  margin-top: 0px; }

.header h2 {
  font-family: TungstenNarrow-Semibold;
  /* letter-spacing: 3px; */
  font-weight: 500;
  font-size: 80px;
