  margin-top: 5px;
  margin-bottom: 10px;
  text-align: left; }

.header .header__message {
  text-align: left;
  max-width: 400px; }

.line_height_text {
  line-height: 30px; }

.page_link {
  margin: 20px 0 10px;
  font-size: 16px; }

.main_message {
  font-size: 17px; }

@media only screen and (min-width: 1200px) {
  .section__container {
    padding-top: 10%; }
  .main_message {
    font-size: 40px;
    line-height: 80px; }
    .main_message p {
      line-height: 60px;
      letter-spacing: 7px;
      width: 500px; }
  .header .header__message {
