    max-width: 800px;
    width: 700px; }
  .header h2 {
    font-size: 160px;
    line-height: 150px; }
  .tagline {
    margin-right: 10%;
    max-width: 40vw;
    width: 40vw; }
    .tagline h1 {
      letter-spacing: 1px;
      margin-top: 0px;
      margin-bottom: 5px;
      opacity: 0.7;
      font-size: 80px;
      font-family: TungstenNarrow-Semibold;
